'use strict';

import fs from 'fs';
import path from 'path';

const { default: template } = require('@marcopeg/template');

import fileUrl from 'file-url';
import { ns } from '../constant/namespace';

const styleTpl = fs.readFileSync(
  path.join(__dirname, './style-tpl.css'),
  'utf8'
);
const imageDir = path.join(__dirname, '../../resources/images');

const style = template(styleTpl, {
  ns,
  vipBtnNormalImageUrl: fileUrl(path.join(imageDir, './vip-btn-normal.png')),
  vipBtnHoverImageUrl: fileUrl(path.join(imageDir, './vip-btn-hover.png')),
  noVipBtnNormalImageUrl: fileUrl(
    path.join(imageDir, './non-vip-btn-normal.png')
  ),
  noVipBtnHoverImageUrl: fileUrl(
    path.join(imageDir, './non-vip-btn-hover.png')
  ),
});

export function generateStyle(): string {
  return style;
}
